import React, { Component } from 'react'; 
import { withRouter } from 'react-router-dom';


//bootstrap
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import CarbonxPages from '../../main/carbonx_pages';

//lang & styles
import './redeem.css';

const thisPage = CarbonxPages.filter(el => el.key==="rdm")[0];

class RedeemPage extends Component {
    // notes:

    render(){
    
        //console.log (thisPage);

        return (
            <>
                <Row><Col>{thisPage.icon}&nbsp; {thisPage.title}</Col></Row>
                <Row><Col><p><br />make sample "cards" of companies that you can trade credits in for. Ex: discount on Clayton home, Pilot/Flying J gas debit cards, Airline miles, etc.</p></Col></Row>

            </>
        );
    }
}


export default withRouter(RedeemPage);
